@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.body{
    display: flex;
    flex-direction: row;
}
.header-container{
    display: flex;
    flex-direction: column;
}

.header-tag{
    padding: 4px 8px;
    border-radius: 100px;
    border: solid 1px #4f46b9;
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #4f46b9;
    font-family: "NanumSquare", sans-serif;
}

.title{
    font-size: 20px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #252a36;
    font-family: "NanumSquare", sans-serif;
}

.title-container{
    margin: 12px 0 36px 0;
}

.card-title{
    margin: 0 0px 16px 0;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #88889d;
}

.card-body{
    margin: 16px 0 36px 0;
    padding: 24px 53px 24px 48px;
    border-radius: 16px;
    border: solid 1px #f2f2f2;
    background-color: #fff;
}
.upload-container{
    display: flex;
    flex-direction: row;
    width: 100%;
}

.label{
    margin: 12px 24px 12px 0;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #242734;
}

.file-input{
    display: flex;
    justify-content: flex-end;
    margin: 0 0 0 24px;
    padding: 4px 4px 4px 0;
    border-radius: 20px;
    border: solid 1px #e3e2f2;
    background-color: #fff;
    width: 100%;
    margin-right: 10%;
  }

  .file-input label{
    padding: 8px 16px;
    border-radius: 20px;
    background-color: #e3e2f2;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #6852c9;
  }

  .file {
    opacity: 0;
    width: 0.1px;
    height: 0.1px;
    position: absolute;
  }
.input-container{
    display: flex;
    flex-direction: row;
    width: 100%;
}

.label{
    margin: 12px 24px 12px 0;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #242734;
}

.input-container input{
    margin: 0 0 0 24px;
  border-radius: 20px;
  border: solid 1px #e3e2f2;
  background-color: #fff;
  /* width: 80%; */
}
.navbar-container{
    display: flex;
    height: 76px;
    padding: 0 0 0 48px;
    margin-left: 250px;
    background-color: #fff;
}

.navbar-container .side-left{
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-container .side-left .menu{
    padding: 28px 24px;
    cursor: pointer;
    transition: 0.07s linear;
    border-bottom: 0px solid #f2c04f;
}

.navbar-container .side-left .menu:hover{
    border-bottom: 2px solid #f2c04f;
}

.navbar-container .side-left .menu span{
    font-size: 14px;
    font-weight: 800;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #252a36;
    font-family: "NanumSquare", sans-serif;
}



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "NanumSquare", sans-serif;
}
.sidebar {
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 78px;
    background-image: linear-gradient(147deg, #755ed3, #725bd1, #4937b0 116%, #4e3bb5 116%);
    padding: 46px 0 611px 24px;
    z-index: 99;
    transition: all 0.5s ease;
}
.sidebar.open {
    width: 250px;
}
.sidebar .logo-details {
    height: 60px;
    display: flex;
    align-items: center;
    position: relative;
}
.sidebar .logo-details .icon {
    opacity: 0;
    transition: all 0.5s ease;
}
.sidebar .logo-details .logo_name {
    color: #fff;
    font-size: 18px;
    font-weight: 800;
    opacity: 0;
    transition: all 0.5s ease;
}
.sidebar.open .logo-details .icon,
.sidebar.open .logo-details .logo_name {
    opacity: 1;
}

.nav-list{
 margin-top: 20px;
}

.sidebar .logo-details #btn {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    font-size: 22px;
    transition: all 0.4s ease;
    font-size: 23px;
    text-align: center;
    cursor: pointer;
    transition: all 0.5s ease;
}
.sidebar.open .logo-details #btn {
    text-align: right;
}
.sidebar i {
    color: #fff;
    height: 60px;
    min-width: 50px;
    font-size: 28px;
    text-align: center;
    line-height: 60px;
}

.sidebar .active-title{
    display: flex;
    flex-direction: row;
    height: 48px;
    margin-top: 38px;
    padding: 14px 30px ;
    border-radius: 100px 0 0 100px;
    background-color: #f2c04f;
}

.sidebar .active-title img{
    width: 20px;
}

.sidebar .active-title .active-text{
    margin: 3px 0 1px 8px;
    font-family: "NanumSquare", sans-serif;
    font-size: 14px;
    font-weight: 800;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
}

.sidebar li {
    position: relative;
    /* margin: 8px 0; */
    list-style: none;
}
.sidebar li .tooltip {
    position: absolute;
    top: -20px;
    left: calc(100% + 15px);
    z-index: 3;
    background: #fff;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    padding: 6px 12px;
    border-radius: 4px;
    font-size: 15px;
    font-weight: 400;
    opacity: 0;
    pointer-events: none;
    transition: 0s;
}
.sidebar li:hover .tooltip {
    opacity: 1;
    pointer-events: auto;
    transition: all 0.4s ease;
    top: 50%;
    transform: translateY(-50%);
}
.sidebar.open li .tooltip {
    display: none;
}
.sidebar input {
    font-size: 15px;
    color: #fff;
    font-weight: 400;
    outline: none;
    height: 50px;
    width: 100%;
    width: 50px;
    border: none;
    border-radius: 12px;
    transition: all 0.5s ease;
    background: #1d1b31;
}
.sidebar.open input {
    padding: 0 20px 0 50px;
    width: 100%;
}
.sidebar .bx-search {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 22px;
    background: #1d1b31;
    color: #fff;
}
.sidebar.open .bx-search:hover {
    background: #1d1b31;
    color: #fff;
}
.sidebar .bx-search:hover {
    background: #fff;
    color: #11101d;
}
.sidebar li a {
    display: flex;
    height: 100%;
    border-radius: 100px 0 0 100px;
    align-items: center;
    text-decoration: none;
    transition: all 0.4s ease;
    margin-left: 24px;
    padding: 12px 0 12px 30px;
}

.sidebar li a:hover {
    background-color: rgba(0, 0, 0, 0.12);
}

.active-menu {
    /* border-radius: 100px 0 0 100px; */
    background-color: rgba(0, 0, 0, 0.12);

}

.sidebar li a .links_name {
    color: #fff;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #fff;
}
.sidebar.open li a .links_name {
    opacity: 1;
    pointer-events: auto;
}
.sidebar li a:hover .links_name,
.sidebar li a:hover i {
    transition: all 0.5s ease;
    /* color: #11101d; */
}
.sidebar li i {
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    border-radius: 12px;
}
.sidebar li.profile {
    position: fixed;
    height: 60px;
    width: 78px;
    left: 0;
    bottom: -8px;
    padding: 10px 14px;
    background: #1d1b31;
    transition: all 0.5s ease;
    overflow: hidden;
}
.sidebar.open li.profile {
    width: 250px;
}
.sidebar li .profile-details {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
}
.sidebar li img {
    height: 45px;
    width: 45px;
    object-fit: cover;
    border-radius: 6px;
    margin-right: 10px;
}
.sidebar li.profile .name,
.sidebar li.profile .job {
    font-size: 15px;
    font-weight: 400;
    color: #fff;
    white-space: nowrap;
}
.sidebar li.profile .job {
    font-size: 12px;
}
.sidebar .profile #log_out {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background: #1d1b31;
    width: 100%;
    height: 60px;
    line-height: 60px;
    border-radius: 0px;
    transition: all 0.5s ease;
}
.sidebar.open .profile #log_out {
    width: 50px;
    background: none;
}
.home-section {
    position: relative;
    background: #f8f8fa;
    min-height: 100vh;
    top: 0;
    left: 78px;
    width: calc(100% - 78px);
    transition: all 0.5s ease;
    z-index: 2;
    padding: 50px;
}
.sidebar.open ~ .home-section {
    left: 250px;
    width: calc(100% - 250px);
}
.home-section .text {
    display: inline-block;
    color: #11101d;
    font-size: 25px;
    font-weight: 500;
    margin: 18px;
}

.button-admin{
    display: flex;
    flex-direction: row;
    width: 192px;
    height: 48px;
    margin: 36px 24px 0 0;
    padding: 14px 84px 14px 36px;
    border-radius: 24px;
    background-color: #f8f8fa;
    cursor: pointer;
}

.button-admin img {
    width: 20px;
    height: 20px;
    margin: 0 8px 0 0;
    object-fit: contain;
}

.button-admin span{
    font-family: "NanumSquare", sans-serif;
    margin: 3px 0 1px 8px;
    font-size: 14px;
    font-weight: 800;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #6852c9;
}
